<template>
  <div>
    <h1 class="mx-12 my-4 grey--text font-weight-medium">
      Kyoto / Pet
    </h1>
    <template>
      <v-tabs
        v-model="tab"
        align-with-title
      >
        <v-tab
          v-for="(tabItem, i) in tabs"
          :key="i"
          :href="`#tab-${i}`"
        >
          {{ tabItem.title }}
        </v-tab>
      </v-tabs>
    </template>
    <template>
      <v-form
        @submit.prevent="onSubmit"
        class="custom-form form-wrapper"
        ref="form"
      >
        <form-card>
          <v-tabs-items v-model="tab">
            <v-tab-item
              v-for="(item, i) in tabs"
              :key="i"
              :value="`tab-${i}`"
            >
              <component
                :is="item.component"
                :data="data"
              />
            </v-tab-item>
          </v-tabs-items>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              color="red accent-4"
              dark
              @click="closeNoSave"
            >
              <v-icon left>
                mdi-close
              </v-icon>
              Close without saving
            </v-btn>
            <v-btn
              color="blue darken-4"
              dark
              @click="saveUpdate"
            >
              Save and update
            </v-btn>
            <v-btn
              color="blue darken-4"
              dark
              type="submit"
            >
              Save and close
            </v-btn>
          </v-card-actions>
        </form-card>
      </v-form>
    </template>
  </div>
</template>

<script>
// import BaseFacility from '@/components/Facilities/BaseFacility'
// import CreateRoomTypeModal from '@/components/Facilities/RoomTypes/CreateModal'
import FacilityRoomTypeEditComponent from '@/components/Facilities/RoomTypes/Edit'

import FormCard from '@/components/Form/FormCard'
export default {
  name: 'FacilityRoomTypesEditing',
  data () {
    return {
      tab: null,
      tabs: [
        {
          title: 'Room type information',
          component: FacilityRoomTypeEditComponent
        }
      ],
      data: {
        roomTypeId: 34,
        typeTitle: '',
        orders: 1,
        screenColor: 1,
        colorType: 'hexa',
        color: '#fff',
        maxCapacity: 1,
        togetherQuantity: 1,
        menu: false
      }
    }
  },
  computed: {
    showColor () {
      return this.color
    }
  },
  methods: {
    onSubmit () {
      if (!this.$refs.form.validate()) {
        return
      }
      // it will be changed when backend is ready
      this.$router.push({ path: '/facility/settings/43224/room-types' })
    },

    closeNoSave () {
      // it will be changed when backend is ready
      this.$router.push({ path: '/facility/settings/43224/room-types' })
    },
    saveUpdate () {
      if (!this.$refs.form.validate()) {
        return
      }
      console.log('Data is updated')
    }
  },
  components: {
    FormCard
  }
}
</script>

