<template>
  <v-container fluid>
    <form-row>
      <template v-slot:key>
        <label class="custom-form-label">
          Room type ID
        </label>
      </template>
      <template v-slot:value>
        {{ data.roomTypeId }}
      </template>
    </form-row>
    <form-row>
      <template v-slot:key>
        <label class="custom-form-label" :for="'room-type-title'">
          Room type name (for management)
        </label>
      </template>
      <template v-slot:value>
        <v-row>
          <v-col cols="12" md="5" lg="3">
            <v-text-field
              id="room-type-title"
              label="Room type name"
              v-model="data.typeTitle"
              :rules="rules.titleRequired"
              persistent-hint
              dense
              outlined
            ></v-text-field>
          </v-col>
        </v-row>
      </template>
    </form-row>
    <form-row>
      <template v-slot:key>
        <label class="custom-form-label" :for="'screen-order-number'">
          Control screen display order
        </label>
      </template>
      <template v-slot:value>
        <v-row>
          <v-col cols="12" md="5" lg="3">
            <v-text-field
              id="screen-order-number"
              type="number"
              v-model="data.orders"
              min="1"
              :rules="rules.minOne"
              persistent-hint
              dense
              outlined
            ></v-text-field>
          </v-col>
        </v-row>
      </template>
    </form-row>
    <form-row>
      <template v-slot:key>
        <label class="custom-form-label" :for="'screen-color'">
          Screen color
        </label>
      </template>
      <template v-slot:value>
        <v-row>
          <v-col cols="12" md="5" lg="3">
            <v-menu
              v-model="data.menu"
              :close-on-content-click="false"
              :nudge-width="200"
              :max-width="300"
              offset-x
            >
              <template v-slot:activator="{ on, attrs }">
                <v-container
                  tag="div"
                  v-bind="attrs"
                  v-on="on"
                  class="color-container"
                  :style="{'background-color': data.color}"
                >
                </v-container>
              </template>

              <v-card>
                <v-color-picker
                  dot-size="13"
                  hide-inputs
                  :mode="data.colorType"
                  swatches-max-height="100"
                  v-model="data.color"
                  width="300"
                ></v-color-picker>
              </v-card>
            </v-menu>
          </v-col>
        </v-row>
      </template>
    </form-row>
    <form-row>
      <template v-slot:key>
        <label class="custom-form-label" :for="'max-capacity'">
          Maximum capacity
        </label>
      </template>
      <template v-slot:value>
        <v-row>
          <v-col cols="12" md="5" lg="3">
            <v-text-field
              id="max-capacity"
              type="number"
              v-model="data.maxCapacity"
              :rules="rules.minOne"
              min="1"
              persistent-hint
              dense
              outlined
            ></v-text-field>
          </v-col>
        </v-row>
      </template>
    </form-row>

    <form-row>
      <template v-slot:key>
        <label class="custom-form-label" :for="'together-quantity'">
          Number of people who can sleep together
        </label>
      </template>
      <template v-slot:value>
        <v-row>
          <v-col cols="12" md="5" lg="3">
            <v-text-field
              id="together-quantity"
              type="number"
              v-model="data.togetherQuantity"
              :rules="rules.minOne"
              min="1"
              persistent-hint
              dense
              outlined
            ></v-text-field>
          </v-col>
        </v-row>
      </template>
    </form-row>
    <form-divider />
    <v-row class="form-row">
      <v-col cols="12" sm="4" md="3" lg="2">
        Delete this facility
      </v-col>
      <v-col cols="12" sm="8" md="9" lg="10">
        If there is no reservation for this facility, you can delete it by clicking the button below.
        <br>
        <v-btn
          class="ma-2"
          outlined
          color="pink"
        >
          Delete
        </v-btn>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import FormRow from '@/components/Form/FormRow'
import FormDivider from '@/components/Form/FormDivider'

export default {
  name: 'FacilityRoomTypeEditComponent',
  components: {
    FormRow,
    FormDivider
  },
  props: ['data'],
  data () {
    return {
      rules: {
        titleRequired: [value => !!value || 'Name is required'],
        minOne: [value => value > 0 || 'Must be minimum 1']
      }
    }
  }
}
</script>

<style scoped lang="scss">
  .color-container {
    min-height: 40px;
    border-radius: 5px;
    margin-bottom: 15px;
    border: 1px solid #969696;
  }
</style>
